<template>
<div ref="scrollTarget" class="test-scroll" style=" background: #f9f9fa">

    <router-view />

</div>
</template>

<script>
// import Home from "./components/Home/Home.vue"
// import Category from '@/components/Category/Category.vue'
export default {
    name: 'App',
    components: {
        // Home,

    },
   
    created() {
        
        function preloaderFadeOutInit() {
            $('.preloader').delay(1000).fadeOut('slow');
            $('body').attr('id', '');

        }
        // Window load function
        jQuery(window).on('load', function () {
            (function ($) {
                preloaderFadeOutInit();
            })(jQuery);
        })

    },
    methods:{
        
    }
}
import Vue from "vue";
Vue.mixin({

    methods: {
        getText(){
            let textLabel = ''
            if(localStorage.locale == 'vi'){
                textLabel = 'Còn lại'
            }else{
                textLabel = ''
            }
            return textLabel
        },
        getFormat(num) {
            if (num == 0) {
                return num
            } else {
                return num / 1000
            }
        },
        getFormattedCurrency(num) {
            if (num == 0) {
                return num
            } else {
                num = num / 1000
                num = (num.toFixed(3))
                var cents = (num - Math.floor(num)).toFixed(2);
                return num
            }

        },
        toDetail(obj, event) {
            // console.log(event, this.holdTime);
            (this.holdTime < 150) && this.$router.push(obj)
        },
        mouseDown() {
            this.startHold = Date.now();
            this.holdTime = 0;
        },
        mouseUp() {
            this.holdTime = Date.now() - this.startHold;
            this.startHold = Date.now();
            // this.startHold = 0;
        },
        getSamePrice(sn) {
            // let sameprice = new int[4];
            // console.log('data get 123', data)
            let sameprice = []
            sameprice[0] = -1;
            sameprice[1] = -1;
            sameprice[2] = -1;
            sameprice[3] = -1;

            if (this.mapMaxDiscount != null) {
                let promotionInfoForm = this.mapMaxDiscount[sn];

                if (promotionInfoForm != null) {
                    // console.log('sameprice 1', sameprice)
                    sameprice[0] = promotionInfoForm.minHourlySamePrice;
                    sameprice[1] = promotionInfoForm.minOvernightSamePrice;
                    sameprice[2] = promotionInfoForm.minDailySamePrice;
                    sameprice[3] = promotionInfoForm.numHours;
                } else {
                    promotionInfoForm = this.mapMaxDiscount[0];
                    if (promotionInfoForm != null) {
                        // console.log('sameprice2', sameprice)
                        sameprice[0] = promotionInfoForm.minHourlySamePrice;
                        sameprice[1] = promotionInfoForm.minOvernightSamePrice;
                        sameprice[2] = promotionInfoForm.minDailySamePrice;
                        sameprice[3] = promotionInfoForm.numHours;
                    }
                }
            }
            // console.log('sameprice', sameprice)
            return sameprice;
        },
        getDiscountMoneyHotel(hasPromotion2, sn, priceHourly, priceOvernight, priceDaily) {
            let discount = []

            if (hasPromotion2 == true) {

                if (this.mapMaxDiscount != []) {

                    var promotionInfoForm = this.mapMaxDiscount[sn]

                    if (promotionInfoForm != null) {
                        discount[0] = this.getCalculatePrice(priceHourly, promotionInfoForm.maxHourlyDiscountMoney, promotionInfoForm.maxHourlyDiscountPercent, promotionInfoForm.maxHourlyPercent)
                        discount[1] = this.getCalculatePrice( //Overnight
                            priceOvernight,
                            promotionInfoForm.maxOvernightDiscountMoney,
                            promotionInfoForm.maxOvernightDiscountPercent,
                            promotionInfoForm.maxOvernightPercent
                        ) //promotionInfoForm.getMaxOvernightDiscountMoney();
                        discount[2] = this.getCalculatePrice( //Daily
                            priceDaily,
                            promotionInfoForm.maxDailyDiscountMoney,
                            promotionInfoForm.maxDailyDiscountPercent,
                            promotionInfoForm.maxDailyPercent) //promotionInfoForm.getMaxDailyDiscountMoney();
                    } else {
                        promotionInfoForm = this.mapMaxDiscount[0]

                        if (promotionInfoForm != null) {
                            discount[0] = this.getCalculatePrice( //Hourly
                                priceHourly,
                                promotionInfoForm.maxHourlyDiscountMoney,
                                promotionInfoForm.maxHourlyDiscountPercent,
                                promotionInfoForm.maxHourlyPercent) //promotionInfoForm.getMaxHourlyDiscountMoney();
                            discount[1] = this.getCalculatePrice( //Overnight
                                priceOvernight,
                                promotionInfoForm.maxOvernightDiscountMoney,
                                promotionInfoForm.maxOvernightDiscountPercent,
                                promotionInfoForm.maxOvernightPercent) //promotionInfoForm.getMaxOvernightDiscountMoney();
                            discount[2] = this.getCalculatePrice( //Daily
                                priceDaily,
                                promotionInfoForm.maxDailyDiscountMoney,
                                promotionInfoForm.maxDailyDiscountPercent,
                                promotionInfoForm.maxDailyPercent) //promotionInfoForm.getMaxDailyDiscountMoney();
                        }
                    }
                }
            }
            return discount;
        },

        getCalculatePrice(price, maxDiscountMoney, maxDiscountMoneyPercent, percent) {
            let value = 0
            /*Only Percent*/
            if (maxDiscountMoney > 0 /*Both*/ && percent > 0) {
                value = this.getCalculateBoth(price, percent, maxDiscountMoneyPercent, maxDiscountMoney)
            } else /*Only Money*/
                if (percent > 0) {
                    value = this.getCalculatePercent(price, percent, maxDiscountMoneyPercent)
                } else if (maxDiscountMoney > 0) {
                value = maxDiscountMoney
            }
            return value
        },
        getCalculateBoth(price, percent, maxMoneyPercent, maxDiscountMoney) {
            let moneyPercent = this.getCalculatePercent(price, percent, maxMoneyPercent)
            if (moneyPercent < maxDiscountMoney) {
                moneyPercent = maxDiscountMoney
            }
            return moneyPercent
        },
        getCalculatePercent(price, percent, maxMoneyPercent) {
            let money = price * percent / 100
            if (money > maxMoneyPercent) {
                money = maxMoneyPercent
            }
            return money
        },
        
        // Get samePrice Hotel No Flash Sale
        getPriceHotelNoFlashSale(data) {
            let samePrice = this.getSamePrice(data.sn)
            let discountPromotion = this.getDiscountMoneyHotel(data.hasPromotion2, data.sn, data.priceHourly, data.priceOvernight, data.priceDaily)
            let getDataPrice = []

            if (data.firstHoursOrigin > 0) {
                getDataPrice[0] = 0
                getDataPrice[1] = data.firstHoursOrigin
                getDataPrice[2] = data.lowestPrice

            } else if (data.overnightOrigin > 0) {
                getDataPrice[0] = 1
                getDataPrice[1] = data.overnightOrigin
                getDataPrice[2] = data.lowestPriceOvernight
            } else if (data.oneDayOrigin > 0) {
                getDataPrice[0] = 2
                getDataPrice[1] = data.oneDayOrigin
                getDataPrice[2] = data.lowestOneDay
            }
            // console.log('sameprice', data)
            if (data.discount == true) {
                // console.log('sameprice directdiscount')
                if (samePrice[0] > -1 || samePrice[1] > -1 || samePrice[2] > -1) {

                    if (samePrice[3] > 0) {
                        if (samePrice[3] == data.firstHours) {
                            getDataPrice[3] = 1 // same price hourly + directdiscount
                            getDataPrice[4] = samePrice[3]
                        }
                    } else {
                        getDataPrice[3] = 2 // same price overnight + directdiscount
                    }
                } else if (data.hasBonusHour > 0) {
                    // console.log('sameprice directbonous', data.hasBonusHour)
                    getDataPrice[3] = 4 // directdiscount + tang gio     
                } else if (data.hasGift == true) {
                    getDataPrice[3] = 5 // directdiscount + tang qua     
                } else if (discountPromotion[0] > 0 || discountPromotion[1] > 0 || discountPromotion[2] > 0) {

                    getDataPrice[3] = 3 // promotion + directdiscount      
                } else {
                    getDataPrice[3] = 6 // directdiscount
                }

                if ((data.firstHoursOrigin != data.priceFirstHours) && (data.firstHoursOrigin > 0)) {
                    getDataPrice[0] = 0
                    getDataPrice[1] = data.firstHoursOrigin
                    getDataPrice[2] = data.lowestPrice
                } else if ((data.overnightOrigin != data.lowestPriceOvernight) && (data.overnightOrigin > 0)) {
                    getDataPrice[0] = 1
                    getDataPrice[1] = data.overnightOrigin
                    getDataPrice[2] = data.lowestPriceOvernight
                } else if ((data.oneDayOrigin != data.lowestOneDay) && (data.oneDayOrigin > 0)) {
                    getDataPrice[0] = 2
                    getDataPrice[1] = data.oneDayOrigin
                    getDataPrice[2] = data.lowestOneDay
                }
            } else if (data.extraFee == true) {

                getDataPrice[3] = 7 // extrafee      
                if (data.firstHoursOrigin > 0) {
                    getDataPrice[0] = 0
                    getDataPrice[2] = data.lowestPrice
                } else if (data.overnightOrigin > 0) {
                    getDataPrice[0] = 1
                    getDataPrice[2] = data.lowestPriceOvernight
                } else if (data.oneDayOrigin > 0) {
                    getDataPrice[0] = 2
                    getDataPrice[2] = data.lowestOneDay
                }
            } else if (samePrice[0] > -1 || samePrice[1] > -1 || samePrice[2] > -1) {

                if (samePrice[3] > 0) {

                    if (samePrice[3] == data.firstHours) {
                        getDataPrice[0] = 0
                        getDataPrice[1] = data.lowestPrice
                        getDataPrice[2] = samePrice[0]
                        getDataPrice[3] = 8 // same price hourly
                        getDataPrice[4] = samePrice[3]
                    }
                } else {
                    getDataPrice[0] = 1
                    getDataPrice[1] = data.lowestPriceOvernight
                    getDataPrice[2] = samePrice[1]
                    getDataPrice[3] = 9 // same price overnight 
                }

            } else if (discountPromotion[0] > 0 || discountPromotion[1] > 0 || discountPromotion[2] > 0) {

                if (data.hasBonusHour > 0) {
                    getDataPrice[3] = 10 // tang gio     
                } else if (data.hasGift == true) {
                    getDataPrice[3] = 11 // tang qua     
                } else {
                    getDataPrice[3] = 12 // promotion  
                }

                if (discountPromotion[0] > 0 && data.firstHoursOrigin > 0) {
                    getDataPrice[0] = 0
                    // console.log('data 1053 else', data)
                    let priceHourlyDiscount = data.lowestPrice - discountPromotion[0]

                    if (priceHourlyDiscount < 0) {
                        priceHourlyDiscount = 0
                    }
                    getDataPrice[1] = data.lowestPrice
                    getDataPrice[2] = priceHourlyDiscount
                } else if (discountPromotion[1] > 0 && data.overnightOrigin > 0) {
                    getDataPrice[0] = 1
                    let priceOvernightDiscount = data.lowestPriceOvernight - discountPromotion[1]
                    if (priceOvernightDiscount < 0) {
                        priceOvernightDiscount = 0
                    }
                    getDataPrice[1] = data.lowestPriceOvernight
                    getDataPrice[2] = priceOvernightDiscount
                } else if (discountPromotion[2] > 0 && data.oneDayOrigin > 0) {
                    getDataPrice[0] = 2
                    let priceOneDayDiscount = data.lowestOneDay - discountPromotion[2]
                    if (priceOneDayDiscount < 0) {
                        priceOneDayDiscount = 0
                    }
                    getDataPrice[1] = data.lowestOneDay
                    getDataPrice[2] = priceOneDayDiscount
                }
            } else {
                if (data.hasBonusHour > 0) {
                    getDataPrice[3] = 10 // tang gio     
                } else if (data.hasGift == true) {
                    getDataPrice[3] = 11 // tang qua     
                }
            }
            // console.log('getDataPrice',getDataPrice)
            // console.log('sameprice price, data.sn', getDataPrice, data.sn)
            return getDataPrice
        },

        // get samePrice roomTypeForm
        getPriceHotelListRoom(roomTypeForm) {
            let samePrice = this.getSamePrice(roomTypeForm.hotelSn)
            let discountPromotion = this.getDiscountMoneyHotel(roomTypeForm.hasPromotion2, roomTypeForm.hotelSn, roomTypeForm.priceFirstHours, roomTypeForm.priceOvernight, roomTypeForm.priceOneDay)
            let getDataPrice = []

            if (roomTypeForm.firstHoursOrigin > 0) {
                getDataPrice[0] = roomTypeForm.firstHoursOrigin
                getDataPrice[1] = roomTypeForm.priceFirstHours
            }
            if (roomTypeForm.overnightOrigin > 0) {
                getDataPrice[2] = roomTypeForm.overnightOrigin
                getDataPrice[3] = roomTypeForm.priceOvernight
            }
            if (roomTypeForm.oneDayOrigin > 0) {
                getDataPrice[4] = roomTypeForm.oneDayOrigin
                getDataPrice[5] = roomTypeForm.priceOneDay
            }
            if (roomTypeForm.discount == true) {
                if ((roomTypeForm.firstHoursOrigin != roomTypeForm.priceFirstHours) && (roomTypeForm.firstHoursOrigin > 0)) {
                    getDataPrice[0] = roomTypeForm.firstHoursOrigin
                    getDataPrice[1] = roomTypeForm.priceFirstHours
                }
                if ((roomTypeForm.overnightOrigin != roomTypeForm.priceOvernight) && (roomTypeForm.overnightOrigin > 0)) {
                    getDataPrice[2] = roomTypeForm.overnightOrigin
                    getDataPrice[3] = roomTypeForm.priceOvernight
                }
                if ((roomTypeForm.oneDayOrigin != roomTypeForm.priceOneDay) && (roomTypeForm.oneDayOrigin > 0)) {
                    getDataPrice[4] = roomTypeForm.oneDayOrigin
                    getDataPrice[5] = roomTypeForm.priceOneDay
                }

            } else if (roomTypeForm.extraFee == true) {
                if (roomTypeForm.firstHoursOrigin > 0) {
                    getDataPrice[1] = roomTypeForm.priceFirstHours
                }
                if (roomTypeForm.overnightOrigin > 0) {
                    getDataPrice[3] = roomTypeForm.priceOvernight
                }
                if (roomTypeForm.oneDayOrigin > 0) {
                    getDataPrice[5] = roomTypeForm.priceOneDay
                }
            } else if (samePrice[0] > -1 || samePrice[1] > -1 || samePrice[2] > -1) {
                getDataPrice[0] = roomTypeForm.priceFirstHours
                getDataPrice[1] = samePrice[0]
                getDataPrice[2] = roomTypeForm.priceFirstHours
                getDataPrice[3] = samePrice[1]
                getDataPrice[4] = roomTypeForm.priceOneDay
                getDataPrice[5] = samePrice[2]
            } else if (discountPromotion[0] > 0 || discountPromotion[1] > 0 || discountPromotion[2] > 0) {
                if (discountPromotion[0] > 0 && roomTypeForm.firstHoursOrigin > 0) {
                    getDataPrice[0] = 0
                    let priceHourlyDiscount = roomTypeForm.priceFirstHours - discountPromotion[0]
                    if (priceHourlyDiscount < 0) {
                        priceHourlyDiscount = 0
                    }
                    getDataPrice[0] = roomTypeForm.priceFirstHours
                    getDataPrice[1] = priceHourlyDiscount
                }
                if (discountPromotion[1] > 0 && roomTypeForm.overnightOrigin > 0) {
                    let priceOvernightDiscount = roomTypeForm.priceOvernight - discountPromotion[1]
                    if (priceOvernightDiscount < 0) {
                        priceOvernightDiscount = 0
                    }
                    getDataPrice[2] = roomTypeForm.priceOvernight
                    getDataPrice[3] = priceOvernightDiscount
                }
                if (discountPromotion[2] > 0 && roomTypeForm.oneDayOrigin > 0) {
                    let priceOneDayDiscount = roomTypeForm.priceOneDay - discountPromotion[2]
                    if (priceOneDayDiscount < 0) {
                        priceOneDayDiscount = 0
                    }
                    getDataPrice[4] = roomTypeForm.priceOneDay
                    getDataPrice[5] = priceOneDayDiscount
                }
            }
            // console.log('getDataPrice', getDataPrice)
            return getDataPrice
        },
        
        // get samePrice Hotel Flash Sale
        getPriceHotelFlashSale(data) {

            let getDataPrice = []
            let roomTypeForm = data.flashSaleRoomTypeForm

            if (roomTypeForm != null) {
                if (roomTypeForm.name != "" && roomTypeForm.sn != 0) {
                    let rooms = roomTypeForm.availableRoom
                    let superSale = roomTypeForm.go2joyFlashSaleDiscount
                    let priceOvernightDiscount = roomTypeForm.priceOvernight
                    if (superSale > 0) {
                        priceOvernightDiscount = priceOvernightDiscount - superSale
                        if (priceOvernightDiscount < 0) {
                            priceOvernightDiscount = 0
                        }
                    }

                    getDataPrice[0] = roomTypeForm.overnightOrigin
                    // console.log('roomTypeForm', roomTypeForm)
                    getDataPrice[1] = priceOvernightDiscount
                } else {
                    getDataPrice[0] = 0

                    getDataPrice[1] = 0
                }
            } else {
                getDataPrice[0] = 0

                getDataPrice[1] = 0
            }
            return getDataPrice
        },

        // get label list hotel Home
        getDiscount(data) {
            let samePrice = this.getPriceHotelNoFlashSale(data)[0]
            let labelDiscount = ''
            let checkNumber = this.getPriceHotelNoFlashSale(data)[3]
            if (data.discount == true) {
                if (checkNumber == 1) {
                    // + same price hourly + directdiscount
                    labelDiscount = 'isSamePriceHourly'
                } else if (checkNumber == 2) {
                    // + same price overnight + directdiscount
                    labelDiscount = 'isSamePriceDaily'

                } else if (checkNumber == 3) {
                    //  console.log('getPriceHotelNoFlashSale discount')
                    // + promotion + directdiscount 
                    if (localStorage.locale == 'vi') {
                        labelDiscount = 'Khuyến mãi'
                    } else {
                        labelDiscount = 'Promotion'
                    }
                } else if (checkNumber == 4) {
                    // + directdiscount + tang gio  
                    if (localStorage.locale == 'vi') {
                        labelDiscount = 'Tặng giờ'
                    } else {
                        labelDiscount = 'Extra hours'
                    }
                } else if (checkNumber == 5) {
                    // + directdiscount + tang qua   
                    if (localStorage.locale == 'vi') {
                        labelDiscount = 'Tặng quà'
                    } else {
                        labelDiscount = 'Special gift'
                    }
                }
                
            }
            // ExtraFee
            else if (data.extraFee == true) {
                if (checkNumber == 7) {
                    if (localStorage.locale == 'vi') {
                        labelDiscount = 'Đồng giá'
                    } else {
                        labelDiscount = 'ExtraFee'
                    }
                }
            } else if (checkNumber == 8) {
                // same price hourly
                labelDiscount = 'isSamePriceHourly'

            } else if (checkNumber == 9) {
                // same price daily
                labelDiscount = 'isSamePriceDaily'
            } else if (checkNumber == 10) {
                // tặng giờ
                if (localStorage.locale == 'vi') {
                    labelDiscount = 'Tặng giờ'
                } else {
                    labelDiscount = 'Extra hours'
                }
            } else if (checkNumber == 11) {
                // tặng quà 
                if (localStorage.locale == 'vi') {
                    labelDiscount = 'Tặng quà'
                } else {
                    labelDiscount = 'Special gift'
                }
            } else if (checkNumber == 12) {
                // Khuyến mãi
                if (localStorage.locale == 'vi') {
                    labelDiscount = 'Khuyến mãi'
                } else {
                    labelDiscount = 'Promotion'
                }
            }
            // console.log('dataHOtel', data.name, samePrice, labelDiscount)
            return labelDiscount
        },

        // 1 get samePrice roomTypeForm
        getLabelRoomTypeForm(roomTypeForm) {
            // console.log('roomTypeForm data', roomTypeForm.hotelSn, roomTypeForm)
            let samePrice = this.getSamePrice(roomTypeForm.hotelSn)
            let discountPromotion = this.getDiscountMoneyHotel(roomTypeForm.hasPromotion2, roomTypeForm.hotelSn, roomTypeForm.priceFirstHours, roomTypeForm.priceOvernight, roomTypeForm.priceOneDay)
            // console.log('discountPromotion', discountPromotion)
            // console.log('roomTypeForm data', roomTypeForm.hotelSn, roomTypeForm)
            // console.log('roomTypeForm samePrice', roomTypeForm.hotelSn, samePrice)
            // console.log('roomTypeForm discountPromotion', roomTypeForm.hotelSn, discountPromotion)
            // ko có data discountPromotion ở một số hotel sẽ bị lỗi

            let getDataPrice = []
            // console.log('getLabelRoomTypeForm data', roomTypeForm)
            // console.log('getLabelRoomTypeForm samePrice', samePrice)
            // console.log('getLabelRoomTypeForm discountPromotion', discountPromotion)
            if (roomTypeForm.firstHoursOrigin > 0) {
                getDataPrice[0] = roomTypeForm.firstHoursOrigin
                getDataPrice[1] = roomTypeForm.priceFirstHours
            }
            if (roomTypeForm.overnightOrigin > 0) {
                getDataPrice[2] = roomTypeForm.overnightOrigin
                getDataPrice[3] = roomTypeForm.priceOvernight
                // console.log('getLabelRoomTypeForm priceOriginOvernight', getDataPrice[2])
                // console.log('getLabelRoomTypeForm priceDefaultOvernight', getDataPrice[3])
            }
            if (roomTypeForm.oneDayOrigin > 0) {
                getDataPrice[4] = roomTypeForm.oneDayOrigin
                getDataPrice[5] = roomTypeForm.priceOneDay
            }
            if (roomTypeForm.discount == true) {
                // console.log('roomTypeForm directDiscount', roomTypeForm.hotelSn, discountPromotion)
                if (samePrice[0] > -1 || samePrice[1] > -1 || samePrice[2] > -1) {
                    if (samePrice[3] > 0) {
                        if (samePrice[3] == roomTypeForm.firstHours) {
                            // console.log('getLabelRoomTypeForm discountSamePriceHour')
                            getDataPrice[6] = 1 // same price hourly + directdiscount
                            getDataPrice[7] = samePrice[3]
                            getDataPrice[8] = samePrice[0]
                        }
                    } else {
                        // console.log('getLabelRoomTypeForm discountSamePriceOvernight')
                        getDataPrice[6] = 2 // same price overnight + directdiscount
                        getDataPrice[8] = samePrice[1]
                    }
                } else if (roomTypeForm.bonusHour > 0) {
                    getDataPrice[6] = 4 // directdiscount + tang gio     
                } else if (roomTypeForm.hasGift == true) {
                    getDataPrice[6] = 5 // directdiscount + tang qua     
                } else if (discountPromotion[0] > 0 || discountPromotion[1] > 0 || discountPromotion[2] > 0) {
                    // console.log('getLabelRoomTypeForm discountDirectdiscount')
                    getDataPrice[6] = 3 // promotion + directdiscount      
                } else {
                    getDataPrice[6] = 6 // directdiscount
                }

                if ((roomTypeForm.firstHoursOrigin != roomTypeForm.priceFirstHours) && (roomTypeForm.firstHoursOrigin > 0)) {
                    getDataPrice[0] = roomTypeForm.firstHoursOrigin
                    getDataPrice[1] = roomTypeForm.priceFirstHours
                }
                if ((roomTypeForm.overnightOrigin != roomTypeForm.priceOvernight) && (roomTypeForm.overnightOrigin > 0)) {
                    getDataPrice[2] = roomTypeForm.overnightOrigin
                    getDataPrice[3] = roomTypeForm.priceOvernight
                }
                if ((roomTypeForm.oneDayOrigin != roomTypeForm.priceOneDay) && (roomTypeForm.oneDayOrigin > 0)) {
                    getDataPrice[4] = roomTypeForm.oneDayOrigin
                    getDataPrice[5] = roomTypeForm.priceOneDay
                }

            } else if (roomTypeForm.extraFee == true) {
                // console.log('getLabelRoomTypeForm extra')
                getDataPrice[6] = 7 // extrafee    
                if (roomTypeForm.firstHoursOrigin > 0) {
                    getDataPrice[1] = roomTypeForm.priceFirstHours
                }
                if (roomTypeForm.overnightOrigin > 0) {
                    getDataPrice[3] = roomTypeForm.priceOvernight
                }
                if (roomTypeForm.oneDayOrigin > 0) {
                    getDataPrice[5] = roomTypeForm.priceOneDay
                }
            } else if (samePrice[0] > -1 || samePrice[1] > -1 || samePrice[2] > -1) {
                // console.log('getLabelRoomTypeForm samePrice')
                if (samePrice[3] > 0) {
                    if (samePrice[3] == roomTypeForm.firstHours) {

                        getDataPrice[6] = 8 // same price hourly
                        getDataPrice[7] = samePrice[3]
                        getDataPrice[8] = samePrice[0]
                    }
                } else {
                    getDataPrice[6] = 9 // same price overnight
                    getDataPrice[8] = samePrice[1]
                }

                getDataPrice[0] = roomTypeForm.priceFirstHours
                if (samePrice[0] > -1) {
                    getDataPrice[1] = samePrice[0]
                    // console.log('getLabelRoomTypeForm samePriceHourly', getDataPrice[1])
                }
                getDataPrice[2] = roomTypeForm.priceOvernight
                if (samePrice[1] > -1) {
                    getDataPrice[3] = samePrice[1]
                    // console.log('getLabelRoomTypeForm samePriceOvernight', getDataPrice[3])
                }
                if (samePrice[2] > -1) {
                    getDataPrice[5] = samePrice[2]
                    // console.log('getLabelRoomTypeForm samePriceDaily', getDataPrice[5])
                }
                getDataPrice[4] = roomTypeForm.priceOneDay
            } else if (discountPromotion[0] > 0 || discountPromotion[1] > 0 || discountPromotion[2] > 0) {
                // console.log('getLabelRoomTypeForm discount')
                if (roomTypeForm.bonusHour > 0) {
                    getDataPrice[6] = 10 // tang gio     
                } else if (roomTypeForm.hasGift == true) {
                    getDataPrice[6] = 11 // tang qua     
                } else {
                    getDataPrice[6] = 12 // promotion  
                }

                if (discountPromotion[0] > 0 && roomTypeForm.firstHoursOrigin > 0) {
                    getDataPrice[0] = 0
                    let priceHourlyDiscount = roomTypeForm.priceFirstHours - discountPromotion[0]
                    if (priceHourlyDiscount < 0) {
                        priceHourlyDiscount = 0
                    }
                    getDataPrice[0] = roomTypeForm.priceFirstHours
                    getDataPrice[1] = priceHourlyDiscount
                }
                if (discountPromotion[1] > 0 && roomTypeForm.overnightOrigin > 0) {
